<template>
  <!--    -->
  <div class="infographic-detail" v-bind:class="{closeDetail : notClickCheck}">
    <div
      class="detail-background"
      v-on:click="clickDetailClose"
    ></div>
    <div class="detail-content">
      <button
        id="detailClose"
        class="closebtn"
        type="button"
        v-on:click="clickDetailClose"
      >&times;</button>
      <div
        class="detail-img"
        v-on:click="clickDetailImageOpen"
        title="   "
      ><img class="detail--img" :src="imgLink" :alt="title">
      </div>
      <div
        v-if="getUID"
        class="detail-info"
      >
        <div class="detail--info">
          <div class="detail-title">
            <h2>{{ title }}</h2>
          </div>
          <div class="detail-desc">
            <p class="infographic--desc">{{ desc }}</p>
          </div>
        </div>
        <div class="detail--info">
          <p class="nota-bene">
              SW 1 
                 
            .
          </p>
          <br/>
          <p class="nota-bene">
               3  6 .
          </p>
          <br>
          <p class="nota-bene">
                   
            <br><span style="color:red;font-weight:bold"></span>    .
          </p>
          <br>
          <p class="nota-bene">
                 .
          </p>
        </div>
        <div class="detail-survey">
          <div id="surveyQ1" class="survey-q">
            <h2 class="q-title">1.  </h2>
            <ul class="q-valuationBasis">
              <li>-&nbsp;  ?</li>
              <li>-&nbsp;     ?</li>
            </ul>
            <div class="q-score">
              <ul class="score-radio-list">
                <li class="score-radio-item">
                  <input
                    class="scoreRadio"
                    type="radio"
                    name="q1"
                    value="3"
                    id="q1-score3"
                    v-model="q1"
                  /><label for="q1-score3">    </label>
                  <div class="check"></div>
                </li>
                <li class="score-radio-item">
                  <input
                    class="scoreRadio"
                    type="radio"
                    name="q1"
                    value="2"
                    id="q1-score2"
                    v-model="q1"
                  /><label for="q1-score2">    </label>
                  <div class="check"></div>
                </li>
                <li class="score-radio-item">
                  <input
                    class="scoreRadio"
                    type="radio"
                    name="q1"
                    value="1"
                    id="q1-score1"
                    v-model="q1"
                  /><label for="q1-score1">    </label>
                  <div class="check"></div>
                </li>
              </ul>
            </div>
          </div>
          <div id="surveyQ2" class="survey-q">
            <h2 class="q-title">2.  </h2>
            <ul class="q-valuationBasis">
              <li>-&nbsp;     ?</li>
              <li>-&nbsp;        ?</li>
              <li>-&nbsp;     ?</li>
            </ul>
            <div class="q-score">
              <ul class="score-radio-list">
                <li class="score-radio-item">
                  <input
                    class="scoreRadio"
                    type="radio"
                    name="q2"
                    value="3"
                    id="q2-score3"
                    v-model="q2"
                  /><label for="q2-score3">  </label>
                  <div class="check"></div>
                </li>
                <li class="score-radio-item">
                  <input
                    class="scoreRadio"
                    type="radio"
                    name="q2"
                    value="2"
                    id="q2-score2"
                    v-model="q2"
                  /><label for="q2-score2">  </label>
                  <div class="check"></div>
                </li>
                <li class="score-radio-item">
                  <input
                    class="scoreRadio"
                    type="radio"
                    name="q2"
                    value="1"
                    id="q2-score1"
                    v-model="q2"
                  /><label for="q2-score1">  </label>
                  <div class="check"></div>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="detail-survey-result">
          <div class="result-wrap">
            <div class="result-q1">
              <h3 v-if="!qErrorCheck(1)">  : {{ getQ1Score }}</h3>
              <h3 v-else>{{ getQ1Score }}</h3>
            </div>
            <div class="result-q2">
              <h3 v-if="!qErrorCheck(2)">  : {{ getQ2Score }}</h3>
              <h3 v-else>{{ getQ2Score }}</h3>
            </div>
          </div>
        </div>
        <div class="detail-survey-submit">
          <button
            v-on:click="onSubmit"
            type="submit"
            id="surveySubmit"
            class="survey-submit"
          >
           </button>
        </div>
      </div>
      <div v-else>
        <div class="detail-info">
          <div class="not-login">
            <p>
              <button
                v-on:click="clickLoginOpen"
              >{{ computedNotLogin }}</button>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'infographic-detail',
  props: {
    clickCheck: Boolean,
    contentId: String,
    title: String,
    desc: String,
    imgLink: String,
  },
  data() {
    return {
      q1: '',
      q2: '',
      q1Error: '',
      q2Error: '',
      submitError: '',
    };
  },
  watch: {
    getSurveyIsOk() {
      if (this.getSurveyIsOk === true) {
        this.clickDetailClose();
      }
    },
    getSurveyOverlapCheck() {
      if (this.getSurveyOverlapCheck === true) {
        this.setNotice({
          noticeTitle: ' ',
          noticeBody: '  ',
          noticeButton: '',
          noticeHeight: '100%',
          noticeDisplay: 'inline-block',
        });
        this.setSurveyOverlapCheckCheck({ surveyOverlapCheckCheckCheck: false });
      }
    },
    getSurveyErrorState() {
      if (this.getSurveyErrorState === '    ') {
        this.setNotice({
          noticeTitle: ' ',
          noticeBody: this.getSurveyErrorState,
          noticeButton: '',
          noticeHeight: '100%',
          noticeDisplay: 'inline-block',
        });
      }
    },
  },
  computed: {
    ...mapGetters(['getUID']),
    ...mapGetters(['getSurveyIsOk']),
    ...mapGetters(['getSurveyErrorState']),
    ...mapGetters(['getSurveyOverlapCheck']),
    ...mapGetters({
      detailImage: 'getDetailImage',
    }),
    computedNotLogin() {
      if (this.detailImage) {
        this.onClickModal({ onModal: false });
      } else {
        this.onClickModal({ onModal: true });
      }
      return ' !';
    },
    notClickCheck() {
      this.clickDetailImageOpen();
      return !this.clickCheck;
    },
    getQ1Score() {
      if (this.qError(1) !== true) {
        return `${this.q1}`;
      }
      return this.q1Error;
    },
    getQ2Score() {
      if (this.qError(2) !== true) {
        return `${this.q2}`;
      }
      return this.q2Error;
    },
  },
  methods: {
    ...mapActions(['onClickModal']),
    ...mapActions(['survey']),
    ...mapActions(['clickDetailImage']),
    ...mapActions(['clickLogin']),
    ...mapActions(['surveySubmitOverlapCheck']),
    ...mapActions(['setNotice']),
    ...mapActions(['surveyPostData']),
    ...mapActions(['initSurvey']),
    ...mapActions(['setSurveyOverlapCheckCheck']),
    clickLoginOpen() {
      this.clickLogin({
        height: '100%',
        display: 'inline-block',
      });
      this.onClickModal({ onModal: true });
    },
    clickDetailImageOpen() {
      this.clickDetailImage({
        detailImage: true,
        detailImgLink: this.imgLink,
        detailImgTitle: this.title,
      });
      this.onClickModal({ onModal: false });
    },
    qErrorCheck(qId) {
      if (this.q1Error !== '' && qId === 1) {
        return true;
      }
      if (this.q2Error !== '' && qId === 2) {
        return true;
      }
      return false;
    },
    qError(qId) {
      if (!this.detailImage) {
        this.onClickModal({ onModal: true });
      }
      if (this.q1 === '' && qId === 1) {
        this.q1Error = '     .';
      } else if (qId === 1) {
        this.q1Error = '';
      }
      if (this.q2 === '' && qId === 2) {
        this.q2Error = '     .';
      } else if (qId === 2) {
        this.q2Error = '';
      }
      return this.qErrorCheck(qId);
    },
    async onSubmit() {
      if (this.getUID === 'admin' || this.getUID === 'dev') {
        this.setNotice({
          noticeTitle: ' ',
          noticeBody: 'admin  dev    ',
          noticeButton: '',
          noticeHeight: '100%',
          noticeDisplay: 'inline-block',
        });
        return;
      }
      if (this.surveySubmitOverlapCheck({ contentId: this.contentId }) === true) {
        this.setNotice({
          noticeTitle: ' ',
          noticeBody: '  ',
          noticeButton: '',
          noticeHeight: '100%',
          noticeDisplay: 'inline-block',
        });
        return;
      }
      if (this.qError(1) || this.qError(2)) {
        this.submitError = '    .';
        return;
      }
      this.surveyPostData({ q1: this.q1, q2: this.q2, contentId: this.contentId });
      this.setNotice({
        noticeTitle: ' ',
        noticeBody: `  : ${this.q1}\n  : ${this.q2}\n  ?`,
        noticeButton: '',
        noticeHeight: '100%',
        noticeDisplay: 'inline-block',
      });
      if (this.getSurveyIsOk === true) {
        this.clickDetailClose();
      } else if (this.getSurveyIsOk === false && this.getSurveyErrorState !== '') {
        this.setNotice({
          noticeTitle: ' ',
          noticeBody: this.getSurveyErrorState,
          noticeButton: '',
          noticeHeight: '100%',
          noticeDisplay: 'inline-block',
        });
      }
    },
    clickDetailClose() {
      this.initSurvey();
      this.$emit('click-detail-close');
    },
  },
};
</script>

<style lang="scss">
@import "@/styles/dsm-infographic/content/infographic-gallery/infographic-detail.scss";
</style>
